@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}

.App{
  background-color: #111;
}
.row{
    color: #fff;
    margin-left: 20px;
}

.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row__posters::-webkit-scrollbar {
  width: 20px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #242424;
  border-radius: 40px;
  border: 3px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #585858;
}

.row__poster{
    width: 100%;
    max-height: 300px;
    margin-right: 10px;
    margin-left: 5px;
    object-fit: contain;
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
}

.row__poster:hover{
    -webkit-transform: scale(1.10);
            transform: scale(1.10);
}


.banner{
    color: #fff;
    object-fit: contain;
    height: 650px;
}

.banner__contents{
    margin-left: 30px;
    padding-top: 240px;
    height: 190px;
}

.banner__title{
    font-size: 4rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__desc{
    width: 45rem;
    line-height: 1.5;
    padding-top: 1rem;
    font-size: 1rem;
    font-weight: 500;
    max-width: 460px;
    height: 80px;
}

.banner__btns{
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.banner__btn{
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2.1rem;
    padding-right: 2.1rem;
    margin-top: 1rem;
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.banner__btn:hover{
    color: #000;
    background-color: #fff;
    transition: all 0.2s;
}

.icons{
    padding-right: 5px;
}

.banner__fade{
    height: 14.5rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
}
.nav{
    position: fixed;
    top: 0;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__black{
    background-color: #111;
}

nav{
    display: flex;
    justify-content: space-between;
}

nav ul{
    display: flex;
    justify-content: center;
    padding: 20px;
    margin: 0 20px;
    align-items: center;
    list-style: none;
}

nav a{
    padding: 0 15px;
    list-style: none;
    color: #9c9c9c;
    text-decoration: none;
}

nav a:hover{
    color: #fff;
}

.nav__logo{
    padding-left: 10px;
    width: 100px;
    object-fit: contain;
}

.user__img{
    padding-right: 10px;
    width: 40px;
    object-fit: contain;
}

@media only screen and (max-width: 1200px){
    .nav__menu{
        display: none;
    }
}

